.nav-main{
    display: flex;
    padding: 15px;
    width: auto;
    height: 10vh;
    justify-content: space-between;
    position:sticky ;
    top: 0;
    left: 0;
    overflow: hidden;
    z-index: 1;
}
.logo{
    width: auto;
}
.logo-pic{
    position: relative;
    bottom: 14px;
    width: 10rem;
}
.item{
    width: auto;
}
.items{
    display: flex;
    gap: 20px;
    font-family:  Volkhov;
    color: whitesmoke;
    font-size: 10px;
}
.item a:hover{
    text-decoration: none;
    color: #e4b7bb;
}
.con-btn{
    position: relative;
    bottom: 1px;
}
.contact{
    padding: 1px 12px;
    width: auto;
    position: relative;
    border-radius: 2rem;
    background-image: linear-gradient(to right, #434343 0%, black 100%);
    color: whitesmoke;
    font-family: Coming Soon;
    font-size: 10px;
}
.contact:hover{
    border-radius: 30px;
    background-image: linear-gradient(to right, #ff8177 0%, #ff867a 0%, #ff8c7f 21%, #f99185 52%, #cf556c 78%, #b12a5b 100%);
    color: #2D2727;
    transform: scale(1.03);
}
