.foot-main{
    width: auto;
    display: flex;
    flex-direction: column;
    padding: 6px;
    background: transparent;
}
/* upper portion */
.up{
    display: flex;
    flex-direction: row;
    height: auto;
    padding: 10px 20px;
    background: #070101;
    color: aliceblue;
    justify-content: space-evenly;
    opacity: .6;
    position: relative;
    border-bottom: 1px solid wheat;
}
.up-info{
font-family: Archivo;
color:rgb(240, 194, 188);
background: transparent;
}
.up-info-text{
    padding: 0%;
    font-size: 12px;
    background: transparent;
}
.up-link{
    background:#070101;
    display: flex;
    gap: 10px;
    position: relative;
    justify-items: space-evenly;
}
.uplinks:hover{
 transform: scale(1.3);
}
.up-link a{
    background: transparent !important;
    padding: 0px;
    margin: 0px;
}
.uplinks{
    height: 20px;
    background: #070101;
}

/* middle portion */
.mid{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-evenly;
}
/* part1 */
.mid-about{
    display: flex;
    flex-direction: column;
    width: auto;
    padding: 10px ;
    justify-content: space-evenly;
    align-items: center;
    border-right: 1.5px solid#3a3636;
}
.pic{
    width: 65px;
    padding: 0;
    border: 2.5px solid crimson;
    border-radius: 5px;
}
.a4{
    padding: 5px;
    font-size: 11px;
    color: aliceblue;
    align-items: center;
    white-space: normal;
    position: relative;
    font-family: Coming Soon;
}
/* part2 */
.mid-info{
width: auto;
padding: 15px;
display: flex;
flex-direction: column;
position: relative;
right: 12px;
justify-content: space-evenly;
border-right: 1.5px solid#3a3636;
}
.a5{
    color: antiquewhite;
    width: auto;
    display: flex;
    font-family: Commissioner;
    font-size: 15px;
    justify-content:space-around;
    white-space: nowrap;
}
.a6{
    width: auto;
    color: rgb(238, 169, 179);
    font-size: 10px;
    font-family: Coming Soon;
    white-space: nowrap;
    opacity: .7;
    justify-content: space-evenly;
}
.a6 a{
    padding: 0 !important;
}
.midicon{
    height: 15px;
    position: relative;
    top: 4px;
    margin-right: 3px;
}
/* part3 */
.mid-contact{
    padding: 10px;
    width: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
.con-head{
    display: flex;
    justify-content: center;
    background: transparent;
}
.a7{
    position: relative;
    justify-content: center;
    font-size: 15px;
    border-radius: 2px;
    color: aliceblue;
    font-family: Commissioner;
}
.form{
    position: relative;
    width: auto;
    background: transparent;
    white-space: nowrap;
}
.ip{
    width: auto;
    margin-right: 2px;
    border-radius: 10px;
    font-size: 8px;
    border: 2px solid crimson;
    color: rgb(163, 174, 184);
    font-family: Coming Soon;
}
.btn-con{
    display: flex;
    justify-content: center;
    background: transparent;
}
.submit{
    width:40px; 
    background: transparent;
}
.submit:hover{
    cursor: pointer;
}
/* lower portion */
.low{
    display: flex;
    justify-content: center;
    background: #070101;
    color: rgb(196, 214, 229);
    height: 20px;
    opacity: .8;
}
.rights{
   position: relative;
   background: transparent;
   white-space: nowrap;
   font-family: Archivo;
   font-size: 10px;
}
