.pro-main{
  width: auto;
  display: flex;
  flex-direction: column;
}
/* header part */
.prohead {
  width: auto;
  padding: 25px;
  display: flex;
  justify-content: space-around;
  color: rgb(255, 255, 255);
}
.a3 {
  padding: 7px;
  border-radius: 20px;
  font-size: 20px;
  background: #221e20;
  font-family: Commissioner;
}
/* content part */
.pro-content{
 width: auto;
}
.projects {
  width: auto;
  color: aliceblue;
  display: flex;
  padding: 30px;
}
/* left part */
.proleft {
  display: flex;
  flex-direction: column;
  padding: 20px;
  justify-content: space-evenly;
  background: #d13131;
}
.proimg{
  height: auto;
  width: auto;
  background: transparent;
}
.imgsource {
  width: 16rem;
  background: transparent;
}
.prolink {
  display: flex;
  justify-content: space-evenly;
  box-shadow: rgba(156, 196, 241, 0.3) 0px 0px 0px 3px;
}
.linkcap {
  position: relative;
  top: 8px;
  font-size: 12px;
  font-family: Commissioner;
  background: transparent;
}
.arrow {
  position: relative;
  top: 3px;
  height: 30px;
  background: transparent;
}
.direction {
  width: 30px;
}
/* right part */
.proright {
  width: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 15px 6%;
  background: #221e20;
}
/* part1 */
.part1{
  width: auto;
  display: flex;
  flex-direction: column;
  gap: 6px;
  text-align: center;
  white-space: nowrap;
  background: #221e20;
}
.title {
  font-size: 16px;
  opacity: 50%;
  font-family: Volkhov;
  border-radius: 10px;
  box-shadow: rgba(156, 196, 241, 0.3) 0px 0px 0px 3px;
}
.stackused {
  font-size: 13px;
  font-family: Commissioner;
  background: transparent;
}
.date {
  font-size: 13px;
  font-family: Archivo;
  background: transparent;
}
/* part2 */
.part2{
  text-align: center;
}
.des {
  font-size: 8px;
  word-spacing: 3px;
  font-family: Coming Soon;
}
