.body-main{
    display: flex;
    width: auto;
    padding: 5px;
}
/* left part  */
.left{
    color: aliceblue;
    width: auto;
    padding: 10px 1rem;
    margin-left: 36px;
    margin-top: 1rem;
    display: block;
}
.h1{
    font-size: 18px;
    color: aliceblue;
    word-spacing: 10px;
    font-family: Archivo;
}
.name{
    font-size: 25px;
    margin-left: 3px;
    color: red;
    font-family: Commissioner;
}
.h2{
    display: flex;
    font-size: 12px;
    word-spacing: 10px;
    font-family: Coming Soon;
    font-style:oblique;
}
.cv-button{
    width: 8rem;
    border-radius: 30px;
    position: relative;
    margin-top: 10px;
    background-image: linear-gradient(to right, #434343 0%, black 100%);
    color: whitesmoke;
    font-family: Volkhov;
}
.cv-button:hover{
    border-radius: 30px;
    background-image: linear-gradient(to right, #ff8177 0%, #ff867a 0%, #ff8c7f 21%, #f99185 52%, #cf556c 78%, #b12a5b 100%);
    color: #2D2727;
    transform: scale(1.04);
}
/* middle part */
.boy{
    width: 70%;
    opacity: 60%;
    border-radius: 50%;
    position: relative;
    left: 2rem;
}
/* right part */
.right{
    width: auto;
    position:relative;
    right: 2rem;
    bottom: 1.5rem;
}
.links{
    display: block;
    width: 24px;
} 
.links:hover{
    transform: scale(1.2);
}

